<template>
    <div class="searchExchange">
        <div class="searchform">
            <div class="search-item category" @click="showFilter('category')" :class="{'active':showCategorySelection}">
                <div class="search-title">物品類型</div>
                <p class="search-desc">小卡、周邊還是手燈？</p>
                 <div class="member-select-form" v-if="showCategorySelection">
                    <el-radio-group v-model="category">
                        <el-radio-button v-for="(category,i) in categories" :label="category"  :value="category" :key="i"></el-radio-button>
                    </el-radio-group>
                    <div class="album-selection" v-if="category === '專卡'">
                        <el-select  v-model="albumGroup">
                            <el-option v-for="(group,i) in groups" :label="group"  :value="group" :key="i"></el-option>
                        </el-select >
                        <el-select  v-model="album" v-if="albumGroup">
                            <el-option v-for="(album,i) in albums" :label="album"  :value="album" :key="i"></el-option>
                        </el-select >
                    </div>
                    <el-input class="keyword"  v-if="category && category!=='專卡'" placeholder="關鍵字" v-model="keyword"></el-input>
                </div>
            </div>
            <div class="search-item" @click="showFilter('ownMember')" :class="{'active':showOwnMemberSelection}">
                <div class="search-title">擁有成員</div>
                <p class="search-desc">{{ownMenberSelection?ownMenberSelection:'選擇成員'}}</p>
                 <div class="member-select-form" v-if="showOwnMemberSelection">
                    <el-radio-group v-model="ownMemberGroup" class="group-select">
                        <el-radio-button class="selection" v-for="(group,i) in groups" :label="group"  :value="group" :key="i"></el-radio-button>
                    </el-radio-group >
                    <el-radio-group v-model="ownMenberSelection">
                        <el-radio class="selection"  border v-for="(member,i) in memberList" :label="member"  :value="member" :key="i"></el-radio>
                    </el-radio-group>
                </div>
            </div>
            <div class="search-item" @click="showFilter('targetMember')" :class="{'active':showTargetMemberSelection}">
                <div class="search-title">欲換成員</div>
                <p class="search-desc">選擇成員</p>
                 <div class="member-select-form" v-if="showTargetMemberSelection">
                    <el-radio-group v-model="targetMemberGroup" class="group-select">
                        <el-radio-button class="selection" v-for="(group,i) in groups" :label="group"  :value="group" :key="i"></el-radio-button>
                    </el-radio-group >
                    <el-radio-group v-model="targetMenberSelection">
                        <el-radio class="selection"  border v-for="(member,i) in memberList" :label="member"  :value="member" :key="i"></el-radio>
                    </el-radio-group>
                </div>
            </div>
            <div class="search" @click="doSearch()">
                <img src="../../../assets/icon/search.svg" alt="">
            </div>
           
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
    data(){
        return {
            form: {
                groupName:'',
                album: '',
                category: '',
                ownMember:[],
                targetMember: [],
            },
            memberList:[],
            albums:[],
            categories:['專卡','玩偶/娃娃','官方周邊','飯制周邊'],
            ways: ['店到店','賣貨便','面交','郵寄','其他'],
            ownMemberGroup:"",
            ownMenberSelection:"",
            targetMemberGroup:"",
            targetMenberSelection:"",
            showCategorySelection: false,
            showOwnMemberSelection:false,
            showTargetMemberSelection: false,
            category:'',
            albumGroup:"",
            album:"",
            keyword:""
        }
    },
    computed:{
        ...mapState({
            groups: state => state.basic.groups,
            nctMembers: state => state.basic.nctMembers,
            nct127Members: state => state.basic.nct127Members,
            nctdreamMembers: state => state.basic.nctdreamMembers,
            wayvMembers: state => state.basic.wayvMembers,
            nctAlbums: state => state.basic.nctAlbums,
            nct127Albums: state => state.basic.nct127Albums,
            nctdreamAlbums: state => state.basic.nctdreamAlbums,
            wayvAlbums: state => state.basic.wayvAlbums,
        }),
    },
    watch:{
        'ownMemberGroup':{
            handler(newValue){
                switch(newValue){
                case 'NCT':
                    this.memberList = this.nctMembers
                    break;
                case 'NCT127':
                    this.memberList = this.nct127Members
                    break;
                case 'NCT Dream':
                    this.memberList = this.nctdreamMembers
                    break;
                case 'WayV':
                    this.memberList = this.wayvMembers
                    break;
            }
            }
        },
        'albumGroup':{
            handler(newValue){
                switch(newValue){
                case 'NCT':
                    this.albums = this.nctAlbums
                    break;
                case 'NCT127':
                    this.albums = this.nct127Albums
                    break;
                case 'NCT Dream':
                    this.albums = this.nctdreamAlbums
                    break;
                case 'WayV':
                    this.albums = this.wayvAlbums
                    break;
            }
            }
        },
        category(newValue){
        }
    },
    methods:{
        initForm(){
            this.form = {
                groupName: '',
                album: '',
                category: '',
                itemType:'',
                exchangeWay: [],
                ownMember:[],
                targetMember: [],
            }
        },
        showFilter(filter){
            this.showCategorySelection = false
            this.showOwnMemberSelection = false
            this.showTargetMemberSelection = false
            switch(filter){
                case 'category':
                    this.showCategorySelection = true
                    break;
                case 'ownMember':
                    this.showOwnMemberSelection = true
                    break;
                case 'targetMember':
                    this.showTargetMemberSelection = true
                    break;
            }
        },
        doSearch(){

        }
    }

}
</script>

